/**
 * Bootstrap Pager
 *
 * @jsx React.DOM
 */

/**
 * Module dependencies
 */
var React = require('react');

var Pager = React.createClass({displayName: 'Pager',

  render: function() {
    return this.transferPropsTo(
      React.DOM.ul( {className:"pager"}, 
        this.props.children
      )
    );
  }

});

module.exports = Pager;
