/** @jsx React.DOM **/

var React = require('react')
  , MarketingLayout = require('../layout/MarketingLayout');

require('../less/pages/login.less');

var SignIn = React.createClass({displayName: 'SignIn',

  render: function() {
    return (
      MarketingLayout(null, 
        React.DOM.div( {className:"container"}, 
          React.DOM.div( {className:"page-header"}, 
            React.DOM.h1(null, "Register")
          ),

          React.DOM.form( {role:"form"}, 
            React.DOM.div( {className:"form-group"}, 
              React.DOM.label( {htmlFor:"name-field"}, "Name"),

              React.DOM.input(
                {className:"form-control",
                id:"name-field",
                placeholder:"John Doe",
                required:true,
                autofocus:true} )

            ),

            React.DOM.div( {className:"form-group"}, 
              React.DOM.label( {htmlFor:"email-field"}, "Email Address"),

              React.DOM.input(
                {className:"form-control",
                id:"email-field",
                type:"email",
                placeholder:"Email address",
                required:true} )

            ),

            React.DOM.div( {className:"form-group"}, 
              React.DOM.label( {htmlFor:"password-field"}, "Password"),

              React.DOM.input(
                {className:"form-control",
                id:"password-field",
                type:"password",
                placeholder:"Password",
                required:true} )

            ),

            React.DOM.div( {className:"form-group"}, 
              React.DOM.label( {htmlFor:"password-confirmation-field"}, 
                "Confirm your password"
              ),

              React.DOM.input(
                {className:"form-control",
                id:"password-confirmation-field",
                type:"password",
                placeholder:"Password",
                required:true} )

            ),

            React.DOM.button( {className:"btn btn-lg btn-primary btn-block", type:"submit"}, 
              "Register"
            )
          )
        )
      )
    );
  }

});

module.exports = SignIn;
