/**
 * Page: /admin/members
 *
 * @jsx React.DOM
 */

/**
 * Module dependencies
 */
var React = require('react');

/**
 * Layouts
 */
var AdminLayout = require('../layout/AdminLayout');

/**
 * Components
 */
var ListItemLink = require('../components/ListItemLink')
  , Button = require('../components/Button')
  , Link = require('../components/Link');

var AdminMembers = React.createClass({displayName: 'AdminMembers',

  getInitialState: function() {
    return { members: [] };
  },

  componentDidMount: function() {
    this._loadMembers();
  },

  render: function() {
    var members = [];

    this.state.members.forEach(function(member) {
      members.push(
        MemberListItem( {key:member.get('id'), member:member} )
      );
    });

    return (
      AdminLayout(null, 
        React.DOM.div( {className:"row"}, 
          React.DOM.div( {className:"col-sm-12"}, 
            React.DOM.ol( {className:"breadcrumb"}, 
              React.DOM.li( {className:"active"}, "Members")
            ),

            React.DOM.div( {className:"list-group"}, 
              members
            )
          )
        )
      )
    );
  },

  _loadMembers: function() {
    var self = this;

    Dropmail.Member.fetch(function(err, data) {
      if (err) return console.error(err);
      self.setState({ members: data.members });
    });
  }

});

/**
 * MemberListItem
 */
var MemberListItem = React.createClass({displayName: 'MemberListItem',

  render: function() {
    var member = this.props.member;

    return (
      React.DOM.div( {className:"list-group-item"}, 
        React.DOM.h4( {className:"list-group-item-heading"}, 
          member.get('name')
        ),

        React.DOM.p( {className:"list-group-item-text"}, 
          "Email: ", React.DOM.a( {href:"mailto:{member.get('email')}"}, member.get('email'))
        ),

        React.DOM.p( {className:"list-group-item-text"}, 
          "Added: ", member.get('created_at')
        )
      )
    );
  }

});

module.exports = AdminMembers;
