body {
  padding-right: 10px;
}
#admin {
  width: 100%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding-bottom: 15px;
}
#admin .text-monospace {
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
  font-size: 0.9em;
}
#admin td > ul.list-inline {
  margin-bottom: 0;
}
.sidebar {
  display: none;
  overflow: hidden;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  padding-right: 10px;
}
.sidebar li {
  white-space: nowrap;
  font-size: 18px;
}
.sidebar li .fa {
  text-align: center;
  width: 1.28571429em;
}
.sidebar li .nav-label {
  font-weight: 200;
  display: none;
}
.sidebar li.active {
  background-color: #f8f8f8;
}
#editor {
  height: 200px;
}
#preview {
  width: 100%;
  border: 0;
  height: 100%;
}
@media (min-width: 768px) {
  #admin {
    padding-left: 65px;
  }
  .sidebar {
    width: 65px;
    display: block;
  }
}
@media (min-width: 992px) {
  #admin {
    padding-left: 160px;
  }
  .sidebar {
    width: 160px;
  }
  .sidebar li .nav-label {
    display: inline-block;
  }
}
h1.page-header {
  margin: 20px 0 20px;
}
.send-preview .message {
  display: inline-block;
  padding-left: 10px;
}
