/**
 * FormField React Component
 *
 * @jsx React.DOM
 */

/**
 * Module dependencies
 */
var React = require('react/addons');

var HelpBlock = React.createClass({displayName: 'HelpBlock',

  render: function() {
    if (this.props.errors) {
      this.props.children = this.props.errors.join(', ');
    }

    if (!this.props.children) return null;

    return (
      React.DOM.div( {className:"help-block"}, 
        this.props.children
      )
    );
  }

});

var FormField = React.createClass({displayName: 'FormField',

  render: function() {
    var ClassSet = React.addons.classSet
      , groupClass = ClassSet({
          'form-group': true,
          'has-error': this._hasError()
        });

    var input = this.transferPropsTo(
      React.DOM.input(
        {className:"form-control",
        name:this.props.name,
        ref:this.props.name,
        type:this.props.type || 'text',
        placeholder:this.props.placeholder} )
    );

    return (
      React.DOM.div( {className:groupClass}, 
        React.DOM.label( {htmlFor:this.props.name}, 
          this.props.label
        ),

        input,

        HelpBlock( {errors:this.props.errors} )
      )
    );
  },

  _hasError: function() {
    return this.props.errors && this.props.errors.length;
  }

});

module.exports = FormField;
