/**
 * Page: /admin/templates
 *
 * @jsx React.DOM
 */

/**
 * Module dependencies
 */
var React = require('react')
  , moment = require('moment');

/**
 * Actions
 */
var PageActions = require('../actions/PageActions');

/**
 * Layouts
 */
var AdminLayout = require('../layout/AdminLayout');

/**
 * Components
 */
var PageHeader = require('../components/PageHeader')
  , Alert = require('../components/Alert')
  , FormField = require('../components/FormField')
  , Button = require('../components/Button')
  , Link = require('../components/Link');

/**
 * AdminTemplates React Class
 */
var AdminTemplates = React.createClass({displayName: 'AdminTemplates',

  getInitialState: function() {
    return { templates: [] };
  },

  componentDidMount: function() {
    this._loadTemplates();
  },

  render: function() {
    var templates = [];

    this.state.templates.forEach(function(template) {
      templates.push(
        TemplateListItem( {key:template.get('id'), template:template} )
      );
    });

    return (
      AdminLayout(null, 
        React.DOM.div( {className:"row"}, 
          React.DOM.div( {className:"col-sm-12"}, 
            React.DOM.ol( {className:"breadcrumb"}, 
              React.DOM.li( {className:"active"}, "Templates")
            ),

            React.DOM.div( {className:"list-group"}, 
              templates
            )
          )
        ),

        React.DOM.div( {className:"row"}, 
          React.DOM.div( {className:"col-sm-12"}, 
            React.DOM.div( {className:"panel panel-default"}, 
              React.DOM.div( {className:"panel-heading"}, 
                React.DOM.h3( {className:"panel-title"}, "New Template")
              ),

              React.DOM.div( {className:"panel-body"}, 
                TemplateForm(null )
              )
            )
          )
        )
      )
    );
  },

  _loadTemplates: function() {
    var self = this;

    Dropmail.Template.fetch(function(err, data) {
      if (err) return console.error(err);
      self.setState({ templates: data.templates });
    });
  }

});

/**
 * TemplateListItem React Class
 */
var TemplateListItem = React.createClass({displayName: 'TemplateListItem',

  render: function() {
    var template = this.props.template
      , updated_at = template.get('updated_at');

    return (
      React.DOM.div( {className:"list-group-item", onClick:this._onClick}, 
        React.DOM.h4( {className:"list-group-item-heading"}, 
          template.get('name')
        ),

        React.DOM.p( {className:"list-group-item-text"}, 
          "Last modified ", moment(updated_at).fromNow()
        )
      )
    );
  },

  _onClick: function() {
    PageActions.goTo('/admin/templates/' + this.props.template.get('id'));
  }

});

/**
 * TemplateForm
 */
var TemplateForm = React.createClass({displayName: 'TemplateForm',

  getInitialState: function() {
    return { errors: {}, saving: false };
  },

  render: function() {
    return (
      React.DOM.form( {onSubmit:this._onSubmit}, 
        FormField(
          {label:"Name",
          placeholder:"User Signup",
          name:"name",
          ref:"nameGroup",
          errors:this.state.errors.name,
          required:true} ),

        Button( {action:"primary", type:"submit", disabled:this.state.saving == true}, 
          "Create"
        )
      )
    );
  },

  _onSubmit: function() {
    var self = this
      , name = this.refs.nameGroup.refs.name.getDOMNode().value;

    this.setState({ saving: true });

    Dropmail.Template.save({ name: name }, function(err, template) {
      if (err) return self.setState({ errors: err.attributes, saving: false });
      PageActions.goTo('/admin/templates/' + template.get('id'));
    });

    return false;
  }

});

module.exports = AdminTemplates;
