/**
 * MarketingLayout
 *
 * @jsx React.DOM
 */

/**
 * Module dependencies
 */
var React = require('react');

/**
 * Actions
 */
var PageActions = require('../actions/PageActions');

/**
 * Components
 */
var NavigationItem = require('../components/NavigationItem');

/**
 * Mixins
 */
var PageStateMixin = require('../mixins/PageStateMixin')
  , AuthorizationStateMixin = require('../mixins/AuthorizationStateMixin');

/**
 * Styles
 */
require('../less/marketing.less');

/**
 * MarketingLayout React Class
 */
var MarketingLayout = React.createClass({displayName: 'MarketingLayout',

  mixins: [
    PageStateMixin,
    AuthorizationStateMixin
  ],

  render: function() {
    return this.transferPropsTo(
      React.DOM.div(null, 
        React.DOM.div( {className:"navbar navbar-inverse navbar-static-top", role:"navigation"}, 
          React.DOM.div( {className:"container"}, 
            React.DOM.div( {className:"navbar-header"}, 
              React.DOM.button( {type:"button", className:"navbar-toggle", 'data-toggle':"collapse", 'data-target':".navbar-collapse"}, 
                React.DOM.span( {className:"sr-only"}, "Toggle navigation"),
                React.DOM.span( {className:"icon-bar"}),
                React.DOM.span( {className:"icon-bar"}),
                React.DOM.span( {className:"icon-bar"})
              ),
              React.DOM.a( {className:"navbar-brand", href:"#"}, "Dropmail")
            ),
            React.DOM.div( {className:"navbar-collapse collapse"}, 
              React.DOM.ul( {className:"nav navbar-nav"}, 
                NavigationItem(
                  {label:"Home",
                  path:"/",
                  currentPage:this.state.currentPage}),

                NavigationItem(
                  {label:"About",
                  path:"/about",
                  currentPage:this.state.currentPage} ),

                NavigationItem(
                  {label:"Contact",
                  path:"/contact",
                  currentPage:this.state.currentPage} )

              ),

              React.DOM.ul( {className:"nav navbar-nav navbar-right"}, 
                NavigationItem(
                  {label:"Sign In",
                  path:"/signin",
                  currentPage:this.state.currentPage} ),

                NavigationItem(
                  {label:"Register",
                  path:"/signup",
                  currentPage:this.state.currentPage} )

              )
            )
          )
        ),

        this.props.children,

        React.DOM.footer(null, 
          React.DOM.div( {className:"container"}, 
            React.DOM.p(null, " Company 2014")
          )
        )
      )
    );
  }

});

module.exports = MarketingLayout;
