/**
 * 404 for /admin/*
 *
 * @jsx React.DOM
 */

/**
 * Module dependancies
 */
var React = require('react');

/**
 * Layouts
 */
var AdminLayout = require('../layout/AdminLayout');

/**
 * Components
 */
var PageHeader = require('../components/PageHeader');

/**
 * AdminNotFound React Class
 */
var AdminNotFound = React.createClass({displayName: 'AdminNotFound',

  render: function() {
    return (
      AdminLayout(null, 
        PageHeader(null, "Page not found")
      )
    );
  }

});

module.exports = AdminNotFound;
