/**
 * Page: /admin/templates/[:id]
 *
 * @jsx React.DOM
 */

/**
 * Module dependencies
 */
var React = require('react/addons')
  , moment = require('moment');

/**
 * Layouts
 */
var AdminLayout = require('../layout/AdminLayout');

/**
 * Components
 */
var ListItemLink = require('../components/ListItemLink')
  , Button = require('../components/Button')
  , Link = require('../components/Link');

/**
 * Mixins
 */

var AuthorizationStateMixin = require('../mixins/AuthorizationStateMixin');

/**
 * AdminTemplate React Class
 */
var AdminTemplate = React.createClass({displayName: 'AdminTemplate',

  mixins: [AuthorizationStateMixin],

  getInitialState: function() {
    return {
      template: new Dropmail.Template,
      revisions: []
    };
  },

  componentDidMount: function() {
    this._fetchTemplate();
  },

  render: function() {
    var self = this
      , template = this.state.template
      , timestampFormat = 'MMMM Do YYYY, h:mm:ss a'
      , revisions = [];

    this.state.revisions.forEach(function(revision) {
      revisions.push(
        TemplateRevisionRow(
          {revision:revision,
          template:template,
          key:revision.get('id'),
          handlePublish:self._publishRevision.bind(null, revision)} )
      );
    });

    return (
      AdminLayout(null, 
        React.DOM.div( {className:"row"}, 
          React.DOM.div( {className:"col-sm-12"}, 
            React.DOM.ol( {className:"breadcrumb"}, 
              ListItemLink( {path:"/admin/templates"}, "Templates"),
              React.DOM.li( {className:"active"}, template.get('name'))
            ),

            React.DOM.p(null, 
              React.DOM.strong(null, "Date created:"), " ", moment(template.get('created_at')).format(timestampFormat)
            ),
            React.DOM.p(null, 
              React.DOM.strong(null, "Last modified:"), " ", moment(template.get('updated_at')).format(timestampFormat)
            ),
            React.DOM.p(null, 
              React.DOM.strong(null, "Latest revision:"), " ", React.DOM.span( {className:"text-monospace"}, template.get('latest_revision'))
            ),
            React.DOM.p(null, 
              React.DOM.strong(null, "Published revision:"), " ", React.DOM.span( {className:"text-monospace"}, template.get('published_revision') || 'not yet published')
            ),

            React.DOM.p(null, 
              Link( {path:'/admin/templates/'+ template.get('id') + '/edit'}, 
                "Edit"
              )
            ),

            React.DOM.div( {className:"panel panel-default"}, 
              React.DOM.div( {className:"panel-heading"}, 
                React.DOM.h3( {className:"panel-title"}, "Revisions")
              ),

              React.DOM.table( {className:"table table-striped table-hover"}, 
                React.DOM.thead(null, 
                  React.DOM.tr(null, 
                    React.DOM.th(null, "User"),
                    React.DOM.th(null, "Date"),
                    React.DOM.th(null, "Revision"),
                    React.DOM.th(null, "Actions")
                  )
                ),

                React.DOM.tbody(null, 
                  revisions
                )

              )
            )
          )
        )

      )
    );
  },

  _fetchTemplate: function() {
    var self = this
      , id = this.props._route.params.id;

    Dropmail.Template.find(id, function(err, template) {
      if (err) return console.error(err);
      self.setState({ template: template });

      // TODO: We should do this in parallel
      self._fetchRevisions();
    });
  },

  _fetchRevisions: function() {
    var self = this;

    this.state.template.revisions(function(err, revisions) {
      if (err) return console.error(err);
      self.setState({ revisions: revisions });
    });
  },

  _publishRevision: function(revision) {
    var self = this;

    revision.publish(function(err, template) {
      if (err) console.error(err);
      self.setState({ template: template });
    });

    return false;
  }

});

/**
 * TemplateRevisionRow
 */
var TemplateRevisionRow = React.createClass({displayName: 'TemplateRevisionRow',

  render: function() {
    var self = this
      , revision = this.props.revision
      , template = this.props.template;

    publishLink = function() {
      if (template.get('published_revision') == revision.get('id')) {
        return (
          Button( {disabled:true, size:"xs"}, 
            "Published"
          )
        );
      }

      return (
        Button( {onClick:self.props.handlePublish, size:"xs"}, 
          "Publish"
        )
      );
    }

    return this.transferPropsTo(
      React.DOM.tr(null, 
        React.DOM.td(null, revision.get('user').get('name')),
        React.DOM.td(null, moment(revision.get('created_at')).format('MMMM Do YYYY')),
        React.DOM.td( {className:"text-monospace"}, revision.get('id')),
        React.DOM.td(null, 
          React.DOM.ul( {className:"list-inline"}, 
            React.DOM.li(null, publishLink())
          )
        )
      )
    );
  }

});

module.exports = AdminTemplate;
