/**
 * SignIn Page
 *
 * @jsx React.DOM
 */

/**
 * Module dependencies
 */

var React = require('react');

/**
 * Layouts
 */
var MarketingLayout = require('../layout/MarketingLayout');

/**
 * Mixins
 */
var ParseRefsMixin = require('../mixins/ParseRefs')
  , AuthorizationStateMixin = require('../mixins/AuthorizationStateMixin');

/**
 * Actions
 */
var AuthorizationActions = require('../actions/AuthorizationActions')
  , PageActions = require('../actions/PageActions');

/**
 * Components
 */
var Alert = require('../components/Alert')
  , FormField = require('../components/FormField');

/**
 * SignIn React Component
 */

var SignIn = React.createClass({displayName: 'SignIn',

  mixins: [
    ParseRefsMixin,
    AuthorizationStateMixin
  ],

  render: function() {
    this.disallowAuthorization();

    return (
      MarketingLayout(null, 
        React.DOM.div( {className:"container"}, 
          React.DOM.div( {className:"page-header"}, 
            React.DOM.h1(null, "Sign In")
          ),

          Alert( {type:"danger"}, 
            this.state.currentAuthorization.errors.message
          ),

          React.DOM.form( {onSubmit:this._handleSubmit, role:"form"}, 
            React.DOM.div( {className:"form-group"}, 
              React.DOM.label( {htmlFor:"email-field"}, "Email address"),

              React.DOM.input(
                {type:"email",
                className:"form-control",
                id:"email-field",
                ref:"email",
                placeholder:"Email address",
                required:true,
                autofocus:true} )

            ),

            React.DOM.div( {className:"form-group"}, 
              React.DOM.label( {htmlFor:"password-field"}, 
                "Password ", React.DOM.a( {href:"/forgot_password"}, "(forgot password)")
              ),

              React.DOM.input(
                {type:"password",
                className:"form-control",
                id:"password-field",
                ref:"password",
                placeholder:"Password",
                required:true} )

            ),

            React.DOM.button( {className:"btn btn-lg btn-primary btn-block", type:"submit"}, 
              "Sign in"
            )
          )
        )
      )
    );
  },

  _handleSubmit: function() {
    AuthorizationActions.signIn(this.parseRefs());
    return false;
  }

});

module.exports = SignIn;
