/**
 * Page: /admin/domains/[:id]
 *
 * @jsx React.DOM
 */

/**
 * Module dependencies
 */
var React = require('react')
  , moment = require('moment');

/**
 * Layouts
 */
var AdminLayout = require('../layout/AdminLayout');

/**
 * Components
 */
var ListItemLink = require('../components/ListItemLink')
  , Button = require('../components/Button')
  , Link = require('../components/Link');

var AdminDomain = React.createClass({displayName: 'AdminDomain',

  getInitialState: function() {
    return { domain: new Dropmail.Domain };
  },

  componentDidMount: function() {
    this._fetchDomain();
  },

  render: function() {
    var domain = this.state.domain;

    return (
      AdminLayout(null, 
        React.DOM.div( {className:"row"}, 
          React.DOM.div( {className:"col-sm-12"}, 
            React.DOM.ol( {className:"breadcrumb"}, 
              ListItemLink( {path:"/admin/domains"}, "Domains"),
              React.DOM.li( {className:"active"}, domain.get('host'))
            ),

            React.DOM.p(null, 
              React.DOM.strong(null, "Status: " ),domain.get('status')
            ),

            React.DOM.p(null, 
              React.DOM.strong(null, "Verification Token: " ), " ", React.DOM.span( {className:"text-monospace"}, domain.get('verification_token'))
            ),

            React.DOM.p(null, 
              React.DOM.strong(null, "Added: " ), " ", moment(domain.get('created_at')).format('MMMM Do YYYY, h:mm:ss a')
            )
          )
        )
      )
    );
  },

  _fetchDomain: function() {
    var self = this
      , id = this.props._route.params.id;

    Dropmail.Domain.find(id, function(err, domain) {
      if (err) return console.error(err);
      self.setState({ domain: domain });
    });

  }

});

module.exports = AdminDomain;
