/**
 * Page: /admin/emails/[:id]
 *
 * @jsx React.DOM
 */

/**
 * Module dependencies
 */
var React = require('react');

/**
 * Layouts
 */
var AdminLayout = require('../layout/AdminLayout');

/**
 * Components
 */
var ListItemLink = require('../components/ListItemLink')
  , Button = require('../components/Button')
  , Link = require('../components/Link');

var AdminEmail = React.createClass({displayName: 'AdminEmail',

  getInitialState: function() {
    return { email: new Dropmail.Email };
  },

  componentDidMount: function() {
    this._fetchEmail();
  },

  render: function() {
    var email = this.state.email;

    return (
      AdminLayout(null, 
        React.DOM.div( {className:"row"}, 
          React.DOM.div( {className:"col-sm-12"}, 
            React.DOM.ol( {className:"breadcrumb"}, 
              ListItemLink( {path:"/admin/emails"}, "Emails"),
              React.DOM.li( {className:"active"}, email.get('id'))
            ),

            React.DOM.p(null, 
              React.DOM.strong(null, "From: " ),email.get('from')
            ),

            React.DOM.p(null, 
              React.DOM.strong(null, "To: " ),email.get('to')
            ),

            React.DOM.p(null, 
              React.DOM.strong(null, "Subject: " ),email.get('subject')
            ),

            React.DOM.p(null, 
              React.DOM.iframe( {id:"preview", ref:"preview"} )
            )
          )
        )
      )
    );
  },

  _fetchEmail: function() {
    var self = this
      , id = this.props._route.params.id;

    Dropmail.Email.find(id, function(err, email) {
      if (err) return console.error(err);
      self.setState({ email: email });
    });

  }

});

module.exports = AdminEmail;
