/**
 * Domains List
 *
 * @jsx React.DOM
 */

/**
 * Module dependancies
 */
var React = require('react');

/**
 * Layouts
 */
var AdminLayout = require('../layout/AdminLayout');

/**
 * Actions
 */
var PageActions = require('../actions/PageActions');

/**
 * Components
 */
var PageHeader = require('../components/PageHeader')
  , FormField = require('../components/FormField')
  , Button = require('../components/Button');

/**
 * AdminNotFound React Class
 */
var AdminDomains = React.createClass({displayName: 'AdminDomains',

  getInitialState: function() {
    return { domains: [] };
  },

  componentDidMount: function() {
    this._loadDomains();
  },

  render: function() {
    var domains = [];

    this.state.domains.forEach(function(domain) {
      domains.push(
        DomainListItem( {key:domain.get('id'), domain:domain} )
      );
    });

    return (
      AdminLayout(null, 
        React.DOM.div( {className:"row"}, 
          React.DOM.div( {className:"col-sm-12"}, 
            React.DOM.ol( {className:"breadcrumb"}, 
              React.DOM.li( {className:"active"}, "Domains")
            ),

            React.DOM.div( {className:"list-group"}, 
              domains
            )
          )
        ),

        React.DOM.div( {className:"row"}, 
          React.DOM.div( {className:"col-sm-12"}, 
            React.DOM.div( {className:"panel panel-default"}, 
              React.DOM.div( {className:"panel-heading"}, 
                React.DOM.h3( {className:"panel-title"}, "Add Domain")
              ),

              React.DOM.div( {className:"panel-body"}, 
                DomainForm(null )
              )
            )
          )
        )
      )
    );
  },

  _loadDomains: function() {
    var self = this;

    Dropmail.Domain.fetch(function(err, data) {
      if (err) return console.error(err);
      self.setState({ domains: data.domains });
    });
  }

});

/**
 * DomainListItem
 */
var DomainListItem = React.createClass({displayName: 'DomainListItem',

  render: function() {
    var domain = this.props.domain;

    return (
      React.DOM.div( {className:"list-group-item", onClick:this._onClick}, 
        React.DOM.h4( {className:"list-group-item-heading"}, 
          domain.get('host')
        ),

        React.DOM.p( {className:"list-group-item-text"}, 
          "Status: ", domain.get('status')
        )
      )
    );
  },

  _onClick: function() {
    PageActions.goTo('/admin/domains/' + this.props.domain.get('id'));
  }

});

/**
 * DomainForm
 */

DomainForm = React.createClass({displayName: 'DomainForm',

  getInitialState: function() {
    return { errors: {}, saving: false }
  },

  render: function() {
    return (
      React.DOM.form( {onSubmit:this._onSubmit}, 
        FormField(
          {label:"Host",
          placeholder:"example.com",
          name:"host",
          ref:"hostGroup",
          errors:this.state.errors.host,
          required:true} ),

        Button( {action:"primary", type:"Add", disabled:this.state.saving == true}, 
          "Create"
        )
      )
    );
  },

  _onSubmit: function() {
    var self = this
      , host = this.refs.hostGroup.refs.host.getDOMNode().value;

    this.setState({ saving: true });

    Dropmail.Domain.save({ host: host }, function(err, domain) {
      if (err) return self.setState({ errors: err.attributes, saving: false });
      PageActions.goTo('/admin/domains/' + domain.get('id'));
    });

    return false;
  }

});

module.exports = AdminDomains;
